<script>
import {
  fetchGetServiceList,
  fetchGetInstanceList,
  fetchGetPfcp,
} from "@/api/demo/index";
export default {
  name: "pfcp",
  data() {
    return {
      serviceName: "",
      serviceOptions: [],
      instanceOptions: [], //upf‰∏ãÈù¢ÁöÑÂÆû‰æã
      instanceValue: "", //ÂΩìÂâçÈÄâ‰∏≠ÁöÑÂÆû‰æã
      isFirst: true,
      loading: false,
      serverData: {},
    };
  },
  mounted() {
    this.getService();
  },
  methods: {
    // Ëé∑ÂèñÊúçÂä°ÂàóË°®
    getService() {
      fetchGetServiceList({
        pageNo: 1,
        pageSize: 50,
      })
        .then((response) => {
          console.log(response);
          this.serviceOptions = response.doms.filter(
            (item) => !item.startsWith("ace")
          );
          if (this.serviceOptions.length > 0) {
            this.serviceName = this.serviceOptions[0];
            this.getServiceInstance(this.serviceName);
          }
        })
        .catch((e) => {
          console.log(e);
          this.$alert(e, "ÁΩëÁªúËØ∑Ê±ÇÂèëÁîüÈîôËØØ", {
            confirmButtonText: "Á°ÆÂÆö",
            callback: (action) => {},
          });
        });
    },
    // ÈÄâÊã©ÊúçÂä°
    changeService(val) {
      if (!val) {
        return;
      }
      this.getServiceInstance(val);
    },
    // ÈÄâÊã©ÂÆû‰æã
    changeInstance(val) {
      // ÂõæË°®ÁΩÆÁ©∫
      this.serverData = [];

      // Ëé∑ÂèñÂëäË≠¶Êï∞ÊçÆ
      this.getPfcp();
    },
    //Ëé∑ÂèñÊúçÂä°‰∏ãÈù¢ÁöÑÂÆû‰æã
    getServiceInstance(serviceName) {
      fetchGetInstanceList({ serviceName: serviceName })
        .then((res) => {
          this.instanceOptions = res.hosts;
          if (this.instanceOptions.length > 0) {
            this.instanceValue = this.instanceOptions[0].instanceId;
            this.changeInstance(this.instanceValue);
          }
        })
        .catch((e) => {
          console.log(e);
          this.$alert(e, "ÁΩëÁªúËØ∑Ê±ÇÂèëÁîüÈîôËØØ", {
            confirmButtonText: "Á°ÆÂÆö",
            callback: (action) => {},
          });
        });
    },
    // Ëé∑ÂèñÊï∞ÊçÆ
    getPfcp() {
      this.loading = true;
      fetchGetPfcp({
        request_type: "pfcp perform",
        instance_id: this.instanceValue,
        sequence_no: 3353,
      })
        .then((response) => {
          this.loading = false;
          console.log("pfcp:", response);
          if ((response != null) & (response != [])) {
            this.serverData = response;
          }
        })
        .catch((e) => {
          this.loading = false;
          console.log(e);
          this.$alert(e, "ÁΩëÁªúËØ∑Ê±ÇÂèëÁîüÈîôËØØ", {
            confirmButtonText: "Á°ÆÂÆö",
            callback: (action) => {},
          });
        });
    },
  },
};
</script>


